// Core

@import "bootstrap/functions";
@import "purpose/functions";

@import "purpose/variables";
@import "bootstrap/variables";

@import "bootstrap/mixins";
@import "purpose/mixins";


// Header

.header {
    width: 100%;
    height: 72px;
    color: #FFFFFF;
    padding: 0 30px 0 2rem;
    z-index: 10;
    display: flex;
    align-items: center;
    top: 0;
    left: 0;
    background: #FFF;
    border-bottom: 1px solid $border-color;

    &:after {
        display: block;
        content: "";
        clear: both;
    }

    .body-backdrop {
        position: absolute;
    }
}

.navigation-trigger {
    float: left;
    padding: 2rem 2rem 2rem 2.4rem;
    margin-left: -2rem;

    &:hover {
        cursor: pointer;
    }

    &.toggled {
        .navigation-trigger-inner {
            transform: rotate(180deg);

            &before {
                transform: scale(1);
            }
        }

        .navigation-trigger-line {
            &:first-child {
                width: 12px;
                transform: translateX(8px) translateY(1px) rotate(45deg);
            }

            &:last-child {
                width: 11px;
                transform: translateX(8px) translateY(-1px) rotate(-45deg);
            }
        }
    }
}

@media(max-width: 1199px) {
    .header {
        position: fixed;
    }
}

@media (max-width: 575px) {
    .header {
        padding-left: 15px;
        padding-right: 15px;
    }
}

.navigation-trigger-inner,
.navigation-trigger-line {
    width: 18px;
    transition: all 300ms;
}

.navigation-trigger-inner {
    position: relative;

    &:before {
        content: '';
        position: absolute;
        width: 40px;
        height: 40px;
        left: -11px;
        top: -14px;
        background-color: rgba(0, 0, 0, 0.25);
        border-radius: 50%;
        transition: all 300ms;
        transform: scale(0);
    }
}

.navigation-trigger-line {
    height: 2px;
    background-color: #333;
    display: block;
    position: relative;

    &:not(:last-child) {
        margin-bottom: 3px;
    }
}

@media (min-width: 1200px) {
    .header-brand {
        min-width: calc(270px - 2rem);
    }
}

.header-brand {
    > h1 {
        line-height: 100%;
        font-size: 1.3rem;
        font-weight: normal;
        margin: 0;
    }

    > h1 > a {
        color: #999;
    }
}

.header-nav {
    .nav-link {
        padding: 0 1rem;
        //font-size: $font-size-sm;
        color: $gray-600;
        transition: background-color 300ms;

        &:hover {
            color: $gray-800;
        }
    }
}

// Main content section

.content-inner {
    margin: auto;
    padding: 30px 15px;
}

.content-inner h3 {
    //margin-bottom: 1.5rem;
}

.content-inner .example {
    margin-bottom: 5rem;
}

@include media-breakpoint-up(xl) {
    .content:not(.content--boxed):not(.content--full) {
        padding: 0 0 0 240px;
    }
}

@include media-breakpoint-between(sm, lg) {
    .content:not(.content--boxed):not(.content--full) {
        padding: 102px 30px 0;
    }
}

@include media-breakpoint-down(xs) {
    .content:not(.content--boxed):not(.content--full) {
        padding: 87px 15px 0;
    }
}

@include media-breakpoint-up(xs) {
    .content--full {
        padding: 102px 30px 0;
    }
}

@include media-breakpoint-down(sm) {
    .content--full {
        padding: 87px 15px 0;
    }

    .content-inner .example .btn {
        margin-bottom: 1rem;
    }
}

// Sidebar

.sidebar {
    width: 240px;
    position: fixed;
    left: 0;
    padding: 0 0.5rem 0.5rem 1.5rem;
    height: 100%;
    overflow: hidden;
    z-index: 1050;
    background-color: $white;
    border-right: 1px solid $border-color;
}

@include media-breakpoint-down(lg) {
    .sidebar {
        transition: transform 300ms, opacity 300ms;

        &:not(.show) {
            opacity: 0;
            transform: translate3d(-100%, 0, 0);
        }

        &.show {
            box-shadow: 5px 0 10px rgba(0, 0, 0, 0.08);
            opacity: 1;
            transform: translate3d(0, 0, 0);
        }

        .scrollbar-inner > .scroll-element {
            margin-right: 0;
        }
    }
}

.sidebar--hidden {
    background-color: #FFFFFF;
    transition: transform 300ms, opacity 300ms;

    &:not(.toggled) {
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
    }

    &.show {
        box-shadow: 5px 0 10px rgba(0, 0, 0, 0.08);
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

.sidebar-brand {
    height: 72px;
    padding: 1rem 0;

    > h1 {
        transform: translateY(50%);
        font-size: 1.09375rem;
        font-weight: normal;
        margin: 0;

        > a {
            color: $gray-800;
        }
    }
}

// Navigation

.navigation {
    list-style: none;
    padding: 0;
    padding-bottom: 4rem;

    .navigation-title {
        color: $gray-500;
        font-weight: 600;
        margin: 0.875rem 0 0;
        padding: 1rem 0rem 0.875rem;
    }

    .navigation-item {
        color: $white;
        font-weight: 500;
    }

    .navigation-link {
        display: block;
        padding: 0.25rem 1rem;
        color: $gray-700;
        font-weight: 500;
        -webkit-transition: background 0.3s, box-shadow 0.3s;
        transition: background 0.3s, box-shadow 0.3s;
        border-radius: $border-radius;
        text-decoration: none;

        &:hover,
        &.active {
            color: theme-color("primary");
        }
    }
}

// Toc nav

.section-nav {
    padding: 0;
    margin: 0;
}
.toc-entry {
    display: block;

    a {
        display: block;
        padding: .125rem 1.5rem;
        color: #99979c;

        &:hover {
            color: #007bff;
            text-decoration: none;
        }
    }

    ul {
        padding-left: 1.5rem;
    }
}

// Page titles

.page-title {
    position: relative;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    border-bottom: 1px solid $border-color;

    .heading {
        margin: 0;
        padding: 0;
        color: $gray-900;

        a {
            color: $gray-900;
        }
    }
}

// Docs content

.content-docs {
    h2[id]::before,
    h3[id]::before,
    h4[id]::before {
        content: "";
        display: block;
        height: 3rem;
        margin-top: -3rem;
    }


    h3[id]:not(:first-child) {
        margin-top: 4rem;
    }

    h2[id],
    h4[id] {
        margin-bottom: 2rem;
    }

    h3[id] {
        margin-bottom: 1rem;
        font-size: $h4-font-size;
    }

    h4[id] {
        margin: 2rem 0 1rem 0;
        font-size: $h6-font-size;
        color: theme-color("primary");
    }
}

.highlight {
    padding: 0;
    background: transparent;
    
    pre {
        //display: none;
        padding: 1.5rem;
        background: $gray-100;
        border: 1px solid $gray-300;
        border-radius: $border-radius;
    }

    &.in {
        pre {
            display: block;
        }
    }
}

.code-example {
    margin: 2rem 0;
    position: relative;

    .btn-see-code {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 100;
        opacity: 0;
    }

    &:hover {
        .btn-see-code {
            opacity: 1;
        }
    }

    > .btn {
        margin-bottom: 1rem;
    }
    > .btn + .btn,
    > .btn-group + .btn-group {
        margin-left: 1rem;
    }
}

.card-columns .code-example {
    margin: 0;
}
.code-clipboard {
    position: relative;
}

.btn-clipboard {
    position: absolute;
    top: .5rem;
    right: .5rem;
    z-index: 10;
    display: block;
    padding: .25rem .5rem;
    font-size: 75%;
    color: #818a91;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    border-radius: .25rem;

    &:hover {
        color: #fff;
        background-color: #027de7;
    }
}
